.card {
  border: 1px solid #e4e9ee;
  background: #f7f8f8;
  width: 350px;
  height: 240px;
  border-radius: 5px;
  color: #8494ab;
  display: flex;
  padding: 15px 25px;
  flex-direction: column;
  position: absolute;
  &__front {
    z-index: 2;
    left: 0;
    top: 0;
  }
  &__back {
    z-index: 1;
    right: 0;
    bottom: 0;
    &::before {
      content: '';
      top: 25px;
      left: 0;
      right: 0;
      height: 42px;
      display: block;
      position: absolute;
      background: #e4e9ee;
    }
  }
  &__row {
    display: flex;
    margin-top: 8px;
    &--cvv {
      margin-left: auto;
      width: 140px;
      &-label {
        margin-top: 80px;
      }
    }
  }
  &__input {
    padding: 14px;
    border: 1px solid #e4e9ee;
  }
  &__number {
    width: 65px;
    margin-right: 9px;
    &:last-child {
      margin-right: 0;
    }
  }
  &-wrapper {
    margin-top: 25px;
    position: relative;
    width: 100%;
    height: 260px;
  }
  &__select {
    border-radius: 0;
    margin-right: 9px;
    padding: 8px 35px 8px 8px;
    font-size: 16px;
    border: 1px solid #e4e9ee;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    background: url(https://slow.im/down-arrow.svg) no-repeat;
    background-size: 14px;
    background-position: right 8px center;
    &:last-child {
      margin-right: 0;
    }
  }
  &__holder {
    width: 100%;
  }
  &__cvv {
    width: 100%;
  }
}